<template>
  <div class="contract-template-edit">
    <h1 class="page-title">{{ isEditing ? '' : '' }}</h1>

    <div class="card">
      <div class="card-body">
        <form @submit.prevent="saveTemplate">
          <div class="mb-3">
            <label for="templateName" class="form-label"> <span class="text-danger">*</span></label>
            <input type="text" v-model="template.name" class="form-control" id="templateName" required>
          </div>

          <div class="row">
            <div class="col-md-6 mb-3">
              <label for="contractType" class="form-label"> <span class="text-danger">*</span></label>
              <input type="text" v-model="template.contract_type" class="form-control" id="contractType" required>
            </div>
            <div class="col-md-6 mb-3">
              <label for="industry" class="form-label"> <span class="text-danger">*</span></label>
              <input type="text" v-model="template.industry" class="form-control" id="industry" required>
            </div>
          </div>

          <div class="mb-3">
            <label for="scene" class="form-label"> <span class="text-danger">*</span></label>
            <input type="text" v-model="template.scene" class="form-control" id="scene" required>
          </div>

          <div class="mb-3">
            <label for="description" class="form-label"> <span class="text-danger">*</span></label>
            <textarea v-model="template.description" class="form-control" id="description" rows="3" required></textarea>
          </div>

          <div class="mb-4">
            <label class="form-label"> <span class="text-danger">*</span></label>
            <div ref="quillEditor" style="height: 400px;"></div>
            <div class="invalid-feedback" :style="{display: contentError ? 'block' : 'none'}">
              
            </div>
          </div>

          <div class="mb-3 form-check">
            <input type="checkbox" v-model="template.is_active" class="form-check-input" id="isActive">
            <label class="form-check-label" for="isActive"></label>
          </div>

          <div class="actions-footer">
            <button type="button" @click="cancel" class="btn btn-secondary me-3">
              
            </button>
            <button type="submit" class="btn btn-primary" :disabled="saving">
              <span v-if="saving" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
              {{ saving ? '...' : '' }}
            </button>
          </div>
        </form>
      </div>
    </div>

    <!--  -->
    <div class="modal fade" id="saveSuccessModal" tabindex="-1" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header bg-success text-white">
            <h5 class="modal-title">
              <i class="bi bi-check-circle me-2"></i>
            </h5>
            <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body text-center py-4">
            <h4></h4>
            <p class="mb-0">{{ isEditing ? '' : '' }}</p>
          </div>
          <div class="modal-footer justify-content-center">
            <button type="button" class="btn btn-success px-4" @click="goToList">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/admin';
import Quill from 'quill';
import 'quill/dist/quill.snow.css';
import { Modal } from 'bootstrap';

export default {
  name: 'ContractTemplateEdit',
  data() {
    return {
      template: {
        name: '',
        contract_type: '',
        industry: '',
        scene: '',
        description: '',
        content: '',
        is_active: true,
      },
      quill: null,
      isEditing: false,
      saving: false,
      contentError: false,
      saveSuccessModal: null,
    };
  },
  async created() {
    const templateId = this.$route.params.id;
    if (templateId) {
      this.isEditing = true;
      await this.fetchTemplateData(templateId);
    }
  },
  mounted() {
    this.initializeQuill();
    this.saveSuccessModal = new Modal(document.getElementById('saveSuccessModal'));
  },
  methods: {
    initializeQuill() {
      if (this.$refs.quillEditor) {
        this.quill = new Quill(this.$refs.quillEditor, {
          theme: 'snow',
          modules: {
            toolbar: [
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              ['bold', 'italic', 'underline', 'strike'],
              [{ 'list': 'ordered'}, { 'list': 'bullet' }],
              [{ 'script': 'sub'}, { 'script': 'super' }],
              [{ 'indent': '-1'}, { 'indent': '+1' }],
              [{ 'direction': 'rtl' }],
              [{ 'size': ['small', false, 'large', 'huge'] }],
              [{ 'color': [] }, { 'background': [] }],
              [{ 'font': [] }],
              [{ 'align': [] }],
              ['clean'],
              ['link', 'image']
            ],
          },
        });
        this.quill.root.innerHTML = this.template.content;
        this.quill.on('text-change', () => {
          this.template.content = this.quill.root.innerHTML;
          this.contentError = false;
        });
      }
    },
    async fetchTemplateData(id) {
      try {
        const response = await api.getContractTemplate(id);
        this.template = response.data;
        if (this.quill) {
          this.quill.root.innerHTML = this.template.content;
        }
      } catch (error) {
        console.error(':', error);
        this.showErrorModal('');
        this.$router.push({ name: 'AdminContractTemplateManagement' });
      }
    },
    async saveTemplate() {
      // Quill
      if (!this.template.content || this.template.content.replace(/<[^>]*>?/gm, '').trim() === '') {
        this.contentError = true;
        return;
      }

      this.saving = true;
      try {
        if (this.isEditing) {
          await api.updateContractTemplate(this.template.id, this.template);
        } else {
          await api.createContractTemplate(this.template);
        }
        this.saveSuccessModal.show();
      } catch (error) {
        console.error(':', error);
        this.showErrorModal('');
      } finally {
        this.saving = false;
      }
    },
    cancel() {
      this.$router.push({ name: 'AdminContractTemplateManagement' });
    },
    goToList() {
      this.saveSuccessModal.hide();
      this.$router.push({ name: 'AdminContractTemplateManagement' });
    },
    showErrorModal(message) {
      // 
      const modalDiv = document.createElement('div');
      modalDiv.innerHTML = `
        <div class="modal fade" id="tempErrorModal" tabindex="-1" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-header bg-danger text-white">
                <h5 class="modal-title"><i class="bi bi-exclamation-triangle me-2"></i></h5>
                <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div class="modal-body text-center py-4">
                <p class="mb-0">${message}</p>
              </div>
              <div class="modal-footer justify-content-center">
                <button type="button" class="btn btn-secondary px-4" data-bs-dismiss="modal"></button>
              </div>
            </div>
          </div>
        </div>`;
      document.body.appendChild(modalDiv);
      
      const errorModal = new Modal(document.getElementById('tempErrorModal'));
      errorModal.show();
      
      // 
      document.getElementById('tempErrorModal').addEventListener('hidden.bs.modal', function () {
        document.body.removeChild(modalDiv);
      });
    }
  },
};
</script>

<style scoped>
.contract-template-edit {
  padding: 2rem;
  background-color: #f8f9fa;
}

.page-title {
  font-weight: 300;
  color: #343a40;
  margin-bottom: 1.5rem;
}

.card {
  border: none;
  box-shadow: 0 0.125rem 0.25rem rgba(0,0,0,.075);
}

.actions-footer {
  padding-top: 1rem;
  border-top: 1px solid #dee2e6;
  display: flex;
  justify-content: flex-end;
}

.text-danger {
  color: #dc3545 !important;
}

/* Quill */
.ql-container.ql-error {
  border-color: #dc3545;
}

.invalid-feedback {
  display: none;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 80%;
  color: #dc3545;
}
</style> 